@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');

* {
    list-style: none;
    text-decoration: none;
    font-family: 'Playfair Display', serif;
}

/* -------------Navigation------------ */

header {
    background-color: #09357b;
    color: white;
    height: 70px;
}

header p:nth-child(1) {
    position: absolute;
    top: 3px;
    left: 15%;
    font-size: 15px;
    color: white;
}

header p:nth-child(2) {
    position: absolute;
    top: 5px;
    left: 14.4%;
    font-size: 23px;
    font-weight: 600;
    color: white;
}

header i {
    position: absolute;
    top: 40px;
    right: 15%;
    opacity: 0.7;
}

input[type='text'] {
    position: absolute;
    top: 30px;
    right: 14.4%;
    opacity: 0.5;
    height: 33px;
    width: 200px;
    border-radius: 7px;
    border: none;
}

input::placeholder {
    font-size: 13px;
    position: relative;
    left: 30px;
    top: 2px;
}

/* -------------------Filter------------------- */

.filterContent {
    width: 71%;
    position: relative;
    left: 14.3%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.filterContent p {
    font-size: 22px;
    font-weight: 900 !important;
}

.dropdownList {
    width: 150px;
    height: 37px;
    font-family: antialiased;
}

/* ---------------------Contents-------------------- */
.mainContents {
    width: 71%;
    position: relative;
    left: 14.3%;
}

/* --------------------- News Contents-------------------- */
.newArticles .newArticlesContent {
    width: 50%;
    background-color: #09357b;
    box-shadow: 2px 2px 5px rgb(164, 156, 156), -2px -2px 5px rgb(164, 156, 156);
    border-bottom: 3px solid green;
    overflow-y: hidden;
}

.newArticles img {
    width: 100%;
    height: 360px;
}

.newArticles .newArticlesContent div {
    height: 93px;
    overflow-y: hidden;
}

.newArticles .newArticlesContent .span1 {
    font-size: 19px;
    color: white;
}

.newArticles .newArticlesContent .span2 {
    color: white;
    font-size: 12px;
}

.newArticles .newArticlesContent:nth-child(2),
.newArticles .newArticlesContent:nth-child(3),
.newArticles .newArticlesContent:nth-child(4),
.newArticles .newArticlesContent:nth-child(5) {
    width: 23.7%;
}

.newArticles .newArticlesContent:nth-child(2) .span1,
.newArticles .newArticlesContent:nth-child(3) .span1,
.newArticles .newArticlesContent:nth-child(4) .span1,
.newArticles .newArticlesContent:nth-child(5) .span1 {
    font-size: 17px;
}

.newArticles .newArticlesContent:nth-child(2) img,
.newArticles .newArticlesContent:nth-child(3) img {
    height: 185px;
}

.newArticles .newArticlesContent:nth-child(2) div,
.newArticles .newArticlesContent:nth-child(3) div {
    height: 125px;
}

.newArticles .newArticlesContent:nth-child(2) {
    position: absolute;
    left: 51.3%;
    top: 0;
    border-bottom: 3px solid red;
}

.newArticles .newArticlesContent:nth-child(3) {
    position: absolute;
    top: 0%;
    right: 0;
    border-bottom: 3px solid gold;
}

.newArticles .newArticlesContent:nth-child(4) {
    position: absolute;
    left: 51.3%;
    top: 26.5%;
    border-bottom: 3px solid blue;
}

.newArticles .newArticlesContent:nth-child(5) {
    position: absolute;
    right: 0;
    top: 26.5%;
    border-bottom: 3px solid green;
}

.newArticles .newArticlesContent div {
    padding: 5px 10px;
}

.newArticles .newArticlesContent:nth-child(4) div,
.newArticles .newArticlesContent:nth-child(5) div {
    margin-top: 20px;
    height: 95px;
}

.newArticles .newArticlesContent:nth-child(4) img,
.newArticles .newArticlesContent:nth-child(5) img {
    display: none;
}

.newArticles .newArticlesContent:nth-child(4) .span2,
.newArticles .newArticlesContent:nth-child(5) .span2 {
    display: none;
}

.newArticles .newArticlesContent:nth-child(6),
.newArticles .newArticlesContent:nth-child(7),
.newArticles .newArticlesContent:nth-child(8) {
    width: 32.3%;
    border-bottom: 3px solid red;
    margin-top: 15px;
}

.newArticles .newArticlesContent:nth-child(6) img,
.newArticles .newArticlesContent:nth-child(7) img,
.newArticles .newArticlesContent:nth-child(8) img {
    height: 220px;
}

.newArticles .newArticlesContent:nth-child(7) {
    position: absolute;
    left: 33.85%;
    top: 36.5%;
}

.newArticles .newArticlesContent:nth-child(8) {
    position: absolute;
    right: 0;
    top: 36.5%;
}

/* --------------------- Sports Contents-------------------- */

.mainContents h2 {
    margin: 50px 0 40px 0;
}

.sportsArticles {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
}

.sportsArticles .sportsArticlesContent {
    width: 32.2%;
    background-color: #09357b;
    box-shadow: 2px 2px 5px rgb(164, 156, 156), -2px -2px 5px rgb(164, 156, 156);
    border-bottom: 3px solid red;
}

.sportsArticles .sportsArticlesContent img {
    width: 100%;
}

.sportsArticles .sportsArticlesContent div {
    padding: 0 10px;
}

.sportsArticles .sportsArticlesContent p {
    font-size: 18px;
    color: white;
}

/* --------------------- footer Contents-------------------- */

footer {
    width: 100%;
    height: 80px;
    background-color: #09357b;
}

/* --------------------- Search Contents-------------------- */

.searchContents {
    width: 71%;
    position: relative;
    left: 14.3%;
    height: 82.3vh;
}

.searchNewArticles {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.searchContents .searchNewArticlesContent {
    width: 19.2%;
    background-color: #09357b;
    box-shadow: 2px 2px 5px rgb(164, 156, 156), -2px -2px 5px rgb(164, 156, 156);
    border-bottom: 3px solid red;
    margin: 5px 0px;
}

.searchContents .searchNewArticlesContent img {
    width: 100%;
}

.searchContents .searchNewArticlesContent div {
    padding: 0 10px;
    color: white;
}

/* ----------------------Spinner----------------------- */

.searchContents .spinner-border,
.mainContents .spinner-border,
.detailedContent .spinner-border {
    height: 85vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.searchContents .spinner-border i,
.mainContents .spinner-border i,
.detailedContent .spinner-border i {
    font-size: 40px;
    animation: rotation 2s infinite ease-in-out;
}

@keyframes rotation {
    100% {
        transform: rotate(360deg);
    }
}

/* ----------------------Detailed Page----------------------- */

.detailedContent {
    width: 71%;
    position: relative;
    left: 14.3%;
}

.detailedContent .detailedArticleContent {
    display: flex;
    height: 84.4vh;
    overflow: hidden;
    align-items: center;
}

.detailedContent .detailedArticleContent div {
    padding: 30px 10px 20px 10px;
}

.detailedContent .detailedArticleContent div>p:nth-child(1) {
    font-size: 15px;
}

.detailedContent .detailedArticleContent div>p:nth-child(3) {
    font-weight: bold;
}

.detailedContent .detailedArticleContent div>p:nth-child(4) {
    font-size: 16px;
    text-align: justify;
    line-height: 25px;
}

/* ============================================================================================ */

@media only screen and (max-width: 480px) {
    header {
        height: 50px;
    }

    header p:nth-child(1) {
        position: absolute;
        top: 0;
        left: 15%;
        font-size: 12px;
    }

    header p:nth-child(2) {
        position: absolute;
        top: 1px;
        left: 13.1%;
        font-size: 19px;
    }

    header i {
        position: absolute;
        top: 28px;
        right: 15%;
    }

    input[type='text'] {
        position: absolute;
        top: 21px;
        right: 13.8%;
        height: 23px;
        width: 150px;
    }

    input::placeholder {
        font-size: 13px;
        position: relative;
        left: 20px;
    }


    .filterContent p {
        font-size: 17px;
    }

    .dropdownList {
        width: 100px;
        height: 24px;
    }

    .newArticles .newArticlesContent,
    .newArticles .newArticlesContent:nth-child(2),
    .newArticles .newArticlesContent:nth-child(3),
    .newArticles .newArticlesContent:nth-child(4),
    .newArticles .newArticlesContent:nth-child(5),
    .newArticles .newArticlesContent:nth-child(6),
    .newArticles .newArticlesContent:nth-child(7),
    .newArticles .newArticlesContent:nth-child(8) {
        position: relative;
        width: 100%;
        border-bottom: 3px solid green;
        left: 0;
        right: 0;
        top: 0;
        margin-bottom: 15px;
    }

    .newArticles .newArticlesContent img,
    .newArticles .newArticlesContent:nth-child(2) img,
    .newArticles .newArticlesContent:nth-child(3) img,
    .newArticles .newArticlesContent:nth-child(4) img,
    .newArticles .newArticlesContent:nth-child(5) img,
    .newArticles .newArticlesContent:nth-child(6) img,
    .newArticles .newArticlesContent:nth-child(7) img,
    .newArticles .newArticlesContent:nth-child(8) img {
        height: 250px;
    }

    .newArticles .newArticlesContent div,
    .newArticles .newArticlesContent:nth-child(2) div,
    .newArticles .newArticlesContent:nth-child(3) div,
    .newArticles .newArticlesContent:nth-child(4) div,
    .newArticles .newArticlesContent:nth-child(5) div,
    .newArticles .newArticlesContent:nth-child(6) div,
    .newArticles .newArticlesContent:nth-child(7) div,
    .newArticles .newArticlesContent:nth-child(8) div {
        min-height: 140px;
    }

    .newArticles .newArticlesContent:nth-child(4) img,
    .newArticles .newArticlesContent:nth-child(5) img {
        display: block;
    }

    .newArticles .newArticlesContent:nth-child(4) .span2,
    .newArticles .newArticlesContent:nth-child(5) .span2 {
        display: block;
    }

    .newArticles .newArticlesContent .span1,
    .newArticles .newArticlesContent:nth-child(2) .span1,
    .newArticles .newArticlesContent:nth-child(3) .span1,
    .newArticles .newArticlesContent:nth-child(4) .span1,
    .newArticles .newArticlesContent:nth-child(5) .span1,
    .newArticles .newArticlesContent:nth-child(6) .span1,
    .newArticles .newArticlesContent:nth-child(7) .span1,
    .newArticles .newArticlesContent:nth-child(8) .span1 {
        font-size: 18px;
    }

    .newArticles .newArticlesContent:nth-child(2),
    .newArticles .newArticlesContent:nth-child(6),
    .newArticles .newArticlesContent:nth-child(7),
    .newArticles .newArticlesContent:nth-child(8) {
        border-bottom: 3px solid red;
    }

    .newArticles .newArticlesContent:nth-child(3) {
        border-bottom: 3px solid gold;
    }

    .newArticles .newArticlesContent:nth-child(4) {
        border-bottom: 3px solid blue;
    }

    .sportsArticles {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .sportsArticles .sportsArticlesContent {
        width: 100%;
        margin-bottom: 15px;
    }

    .searchContents {
        height: min-content;
    }

    .searchNewArticles {
        display: flex;
        flex-direction: column;
    }

    .searchContents .searchNewArticlesContent {
        width: 100%;
    }

    footer {
        height: 50px;
    }

    .detailedContent .detailedArticleContent {
        display: flex;
        flex-direction: column-reverse;
        height: min-content;
    }
}

/* =========================================================================================================== */

@media only screen and (min-width: 481px) and (max-width: 1024px) {
    header {
        height: 60px;
    }

    header p:nth-child(1) {
        position: absolute;
        top: 1;
        left: 14%;
        font-size: 13px;
    }

    header p:nth-child(2) {
        position: absolute;
        top: 3px;
        left: 13.1%;
        font-size: 20px;
    }

    header i {
        position: absolute;
        top: 36px;
        right: 15%;
    }

    input[type='text'] {
        position: absolute;
        top: 27px;
        right: 13.8%;
        height: 27px;
        width: 160px;
    }

    input::placeholder {
        position: relative;
        left: 23px;
    }

    .filterContent p {
        font-size: 20px;
    }

    .dropdownList {
        width: 120px;
        height: 28px;
    }


    .newArticles {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .newArticles .newArticlesContent,
    .newArticles .newArticlesContent:nth-child(2),
    .newArticles .newArticlesContent:nth-child(3),
    .newArticles .newArticlesContent:nth-child(4),
    .newArticles .newArticlesContent:nth-child(5),
    .newArticles .newArticlesContent:nth-child(6),
    .newArticles .newArticlesContent:nth-child(7),
    .newArticles .newArticlesContent:nth-child(8) {
        position: relative;
        width: 49%;
        border-bottom: 3px solid green;
        left: 0;
        right: 0;
        top: 0;
        margin-bottom: 12px;
    }

    .newArticles .newArticlesContent img,
    .newArticles .newArticlesContent:nth-child(2) img,
    .newArticles .newArticlesContent:nth-child(3) img,
    .newArticles .newArticlesContent:nth-child(4) img,
    .newArticles .newArticlesContent:nth-child(5) img,
    .newArticles .newArticlesContent:nth-child(6) img,
    .newArticles .newArticlesContent:nth-child(7) img,
    .newArticles .newArticlesContent:nth-child(8) img {
        height: 225px;
    }

    .newArticles .newArticlesContent div,
    .newArticles .newArticlesContent:nth-child(2) div,
    .newArticles .newArticlesContent:nth-child(3) div,
    .newArticles .newArticlesContent:nth-child(4) div,
    .newArticles .newArticlesContent:nth-child(5) div,
    .newArticles .newArticlesContent:nth-child(6) div,
    .newArticles .newArticlesContent:nth-child(7) div,
    .newArticles .newArticlesContent:nth-child(8) div {
        height: 150px;
    }

    .newArticles .newArticlesContent .span1,
    .newArticles .newArticlesContent:nth-child(2) .span1,
    .newArticles .newArticlesContent:nth-child(3) .span1,
    .newArticles .newArticlesContent:nth-child(4) .span1,
    .newArticles .newArticlesContent:nth-child(5) .span1,
    .newArticles .newArticlesContent:nth-child(6) .span1,
    .newArticles .newArticlesContent:nth-child(7) .span1,
    .newArticles .newArticlesContent:nth-child(8) .span1 {
        font-size: 17px;
    }

    .newArticles .newArticlesContent:nth-child(4) img,
    .newArticles .newArticlesContent:nth-child(5) img {
        display: block;
    }

    .newArticles .newArticlesContent:nth-child(4) .span2,
    .newArticles .newArticlesContent:nth-child(5) .span2 {
        display: block;
    }

    .newArticles .newArticlesContent:nth-child(6),
    .newArticles .newArticlesContent:nth-child(7),
    .newArticles .newArticlesContent:nth-child(8) {
        margin-top: 0px;
    }

    .sportsArticles {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .sportsArticles .sportsArticlesContent {
        width: 49%;
        margin-bottom: 12px;
    }

    footer {
        height: 60px;
    }

    .searchContents {
        height: min-content;
    }

    .searchContents h2 {
        font-size: 18px;
    }

    .searchNewArticles {
        display: flex;
        justify-content: space-between;
    }

    .searchContents .searchNewArticlesContent {
        width: 49%;
    }

    .detailedContent .detailedArticleContent {
        display: flex;
        flex-direction: column-reverse;
        height: min-content;
    }
}